:root {
	--font-size: 22px;

	--info_panel_height: calc(var(--font-size)*2);
	--main_gap: calc(var(--font-size) / 2);

	/*
	 This properties will be possible changed in run time in
	 AppCode.js componentDidMount().
	============================================================= */
	--screen-height: 100vh;
	--screen-width: 100vw;
	--screen-padding: 8px;
	/* ========================================================== */

	--text-height-portrait: calc(

			(
				var(--screen-height) - var(--screen-padding) - calc(var(--main_gap) * 2) -
					calc(var(--info_panel_height)*1)
			) / 2
	);

	--text-height-landscape: calc(
		var(--screen-height) - calc(var(--info_panel_height)*1) - var(--main_gap) -
			var(--screen-padding)
	);

	--settings-color: #522719; /* #add8e6; */
	--background-color: #000000; /* #ffffff; */
	--base-text-color: #ffffff; /* #000000; */
	--danger-color: #ff4000; /* #00bfff */
	--english-appcode__speaking-text-color: #6b5ee0; /*  #94a11f */
	--english-text-color: #94a11f; /* #6b5ee0 */
	--grey-color: #777777; /*  #888888 */
	--shadow-color: #dddddd; /* #222222 */
}

/* body
============================================================= */

body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	background-color: #000000;
	background-color: var(--background-color);

	-webkit-user-select: none;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-tap-highlight-color: transparent;
}

/* grid
============================================================= */

.appcode__grid {
	background-color: #000000;
	background-color: var(--background-color);
	color: #ffffff;
	color: var(--base-text-color);
	display: grid;
	-webkit-filter: invert(100%);
	        filter: invert(100%);
	font-size: 22px;
	font-size: var(--font-size);
	grid-column-gap: calc(22px / 2);
	grid-column-gap: var(--main_gap);
	grid-row-gap: calc(22px / 2);
	grid-row-gap: var(--main_gap);
	margin: 0;
	padding: 0 8px;
	padding: 0 var(--screen-padding);
	font-family: sans-serif;
}

@media (orientation: portrait) {
	.appcode__grid {
		grid-template-columns: 1fr;
		grid-template-rows: calc(

			(
				100vh - 8px - calc(calc(22px / 2) * 2) -
					calc(calc(22px*2)*1)
			) / 2
	) calc(22px*2)
			calc(

			(
				100vh - 8px - calc(calc(22px / 2) * 2) -
					calc(calc(22px*2)*1)
			) / 2
	);
		grid-template-rows: var(--text-height-portrait) var(--info_panel_height)
			var(--text-height-portrait);
		grid-template-areas: 'russian' 'info' 'english';
	}
	.appcode__icon_rotate {
		-webkit-transform: rotate(270deg);
		        transform: rotate(270deg);
		margin-left: 7px;
	}
	.appcode__icon_rotate_back {
		-webkit-transform: rotate(90deg);
		        transform: rotate(90deg);
		margin-right: 7px;
	}
	.appcode__scroll {
		max-height: 100%;
		width: calc(100vw - 8px*2);
		width: calc(var(--screen-width) - var(--screen-padding)*2);
		overflow-y: scroll;
		overflow-wrap: break-word;
	}
}

@media (orientation: landscape) {
	.appcode__grid {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: calc(
		100vh - calc(calc(22px*2)*1) - calc(22px / 2) -
			8px) calc(22px*2);
		grid-template-rows: var(--text-height-landscape) var(--info_panel_height);
		grid-template-areas: 'russian english' 'info info';
	}
	.appcode__icon_rotate {
		-webkit-transform: rotate(90deg);
		        transform: rotate(90deg);
	}
	.appcode__icon_rotate_back {
		-webkit-transform: rotate(270deg);
		        transform: rotate(270deg);
	}
	.appcode__scroll {
		max-height: 100%;
		width: calc(100vw/2 - calc(22px / 2));
		width: calc(var(--screen-width)/2 - var(--main_gap));
		overflow-y: scroll;
		overflow-wrap: break-word;
	}
}

.appcode__info {
	grid-area: info;
}

.appcode__russian {
	grid-area: russian;
}

.appcode__english {
	grid-area: english;
}

/* font size and colors
============================================================= */

.appcode__eng_text_color {
	color: #94a11f;
	color: var(--english-text-color);
}

.appcode__speaking {
	color: #6b5ee0;
	color: var(--english-appcode__speaking-text-color);
}

/* icons
============================================================= */

.appcode__icon_invert__horizontal {
	-webkit-transform: scaleX(-1);
	        transform: scaleX(-1);
}

/* flex boxes
============================================================= */

.appcode__center {
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.appcode__info2 {
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}

/*   PopupWindow.css   ********************************************************** */

.popup__full_screen_div {
	-webkit-align-items: center;
	        align-items: center;
	bottom: 0;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	left: 0;
	overflow: hidden;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 5;
}

.popup__full_screen_div_opacity {
	background-color: #777777;
	background-color: var(--grey-color);
	bottom: 0;
	left: 0;
	opacity: 0.4;
	overflow: hidden;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 4;
}

.popup__opacity_1 {
	opacity: 1;
	transition: opacity 0.9s ease;
}
.popup__opacity_0 {
	opacity: 0;
	transition: opacity 0.9s ease;
}

.popup__window {
	/* max-height: calc(100vh - 4rem); */
	background-color: #000000;
	background-color: var(--background-color);
	border-radius: 5px;
	box-shadow: #dddddd 0px 13px 26px 0px;
	box-shadow: var(--shadow-color) 0px 13px 26px 0px;
	padding: 10px 25px 10px 20px;
	position: relative;
}

.popup__list {
	/* max-height: calc(100vh - 4rem); */
	overflow-y: scroll;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	max-width: 10em;
}

.popup__cancel {
	position: absolute;
	top: 2px;
	right: 10px;
	cursor: pointer;
}

.popup__row {
	padding: 0.6rem 0.4rem;
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

.popup__row_selected {
	background-color: #ff4000;
	background-color: var(--danger-color);
	border-radius: 5px;
}

.popup__gap {
	padding-right: 10px;
}

.popup__scroll {
	max-height: 80vh;
	overflow-y: scroll;
	/* -webkit-overflow-scrolling: touch; */
}

